import React from 'react';
import Head from './Head';
import { Link } from 'react-router-dom';
import emptyLibraryIMG from '../img/empty-library-img.jpg';
import cookieSVG from '../img/cookie.svg';
import accountCircleSVG from '../img/account-circle.svg';
import rocketIconSVG from '../img/rocket-icon.svg';
//Redux
import { useSelector, useDispatch } from 'react-redux';
import { updateInformations } from '../reduxStore/login';

const Library = () => {
  //Redux
  const dataUser = useSelector((state) => state.user);
  const dispatch = useDispatch();

  let library;
  let _id;

  if (dataUser.data !== null) {
    library = dataUser.data.user.library;
    _id = dataUser.data.user._id;
  }

  function handleClearData() {
    let informations = {
      library: [['empty']],
    };

    dispatch(updateInformations(informations, _id));
  }

  return (
    <>
      <Head
        title="Library"
        description="See games that you added to your account"
      />

      <h2>My Games</h2>

      <section>
        {/* Feeded Library container */}
        {library[0][0] !== 'empty' && (
          <div id="games-container">
            {library.map((game) => {
              return (
                <Link
                  to={'/store/' + game[0]}
                  key={game[0]}
                  className="game-card"
                >
                  <p id="name">{game[1]}</p>
                  <p id="price-library">$ {game[2]}</p>
                </Link>
              );
            })}
            <button
              id="button-clear-library"
              onClick={() => {
                handleClearData();
              }}
            >
              <img src={cookieSVG} alt="cookie icon" />
              Clear Library
            </button>
            <p className="advisory">
              This is a fictional project, you didn't bought anything during
              that!
            </p>
          </div>
        )}

        {/* Empty Library container */}
        {library[0][0] === 'empty' && (
          <div>
            <div className="empty-card">
              <p>It's kinda empty here</p>
            </div>
            <img
              src={emptyLibraryIMG}
              alt="Ilustration for empty Library"
              className="empty-img"
            />
          </div>
        )}
      </section>
      {library[0][0] !== 'empty' && (
        <section className="tankhs">
          <p>THANKS FOR YOUR ATTENTION!</p>
        </section>
      )}
      <section className="info">
        <div>
          <img src={accountCircleSVG} alt="account-circle img" />
          <p>This website uses localStorage for user informations</p>
        </div>
        <div>
          <img src={rocketIconSVG} alt="rocket-icon img" />
          <p>
            Feel free to test every feature and call me to making a website for
            you
          </p>
        </div>
      </section>
      <section className="tankhs">
        <p>THANKS FOR YOUR ATTENTION!</p>
      </section>
    </>
  );
};

export default Library;
