import { createSlice } from '@reduxjs/toolkit';

const slice = createSlice({
  name: 'login',
  initialState: {
    token: {
      data: null,
      loading: false,
      error: false,
    },
    user: {
      data: null,
      loading: false,
      error: false,
    },
  },
  reducers: {
    fetchTokenStarted(state) {
      state.token.loading = true;
      state.token.error = false;
    },
    fetchTokenSuccess(state, action) {
      state.token.data = action.payload;
      state.token.loading = false;
      state.token.error = false;
    },
    fetchTokenFailed(state, action) {
      state.token.data = null;
      state.token.loading = false;
      state.token.error = action.payload;
    },
    fetchUserStarted(state) {
      state.user.loading = true;
      state.user.error = false;
    },
    fetchUserSuccess(state, action) {
      state.user.data = action.payload;
      state.user.loading = false;
      state.user.error = false;
    },
    fetchUserFailed(state, action) {
      state.user.data = null;
      state.user.loading = false;
      state.user.error = action.payload;
    },
  },
});

const {
  fetchTokenStarted,
  fetchTokenSuccess,
  fetchTokenFailed,
  fetchUserStarted,
  fetchUserSuccess,
  fetchUserFailed,
} = slice.actions;

const fetchRegister = (user) => async (dispatch) => {
  try {
    dispatch(fetchTokenStarted());
    const resp = await fetch(
      'https://lucyna-rest-api.onrender.com/auth/register',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(user),
      },
    );
    const data = await resp.json();
    return dispatch(fetchTokenSuccess(data));
  } catch (error) {
    return dispatch(fetchTokenFailed(error.message));
  }
};

export const fetchLogin = (user) => async (dispatch) => {
  dispatch(fetchUserStarted());
  const resp = await fetch(
    'https://lucyna-rest-api.onrender.com/auth/authenticate',
    {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(user),
    },
  );
  const data = await resp.json();

  console.log(data);

  if (resp.status === 400) {
    return dispatch(fetchUserFailed(data));
  }

  return dispatch(fetchUserSuccess(data));
};

export const registerFetch = (user) => async (dispatch) => {
  try {
    const { payload } = await dispatch(fetchRegister(user));
    if (payload.token !== undefined) await dispatch(fetchUserSuccess(payload));
    if (payload.token !== undefined) {
      localStorage.setItem('token', payload.token);
    }
  } catch (error) {
    console.log(error);
  }
};

export const loginFetch = (user) => async (dispatch) => {
  try {
    const { payload } = await dispatch(fetchLogin(user));
    if (payload.token !== undefined) await dispatch(fetchUserSuccess(payload));
    if (payload.token !== undefined)
      localStorage.setItem('token', payload.token);
  } catch (error) {
    console.log('deu erro');
  }
};

export const updateInformations = (newInformation, _id) => async (dispatch) => {
  try {
    dispatch(fetchUserStarted());
    const resp = await fetch(
      `https://lucyna-rest-api.onrender.com/auth/user/${_id}`,
      {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newInformation),
      },
    );
    const data = await resp.json();
    return dispatch(fetchUserSuccess(data));
  } catch (error) {
    return dispatch(fetchUserFailed(error.message));
  }
};

const fetchLoginByToken = (user) => async (dispatch) => {
  try {
    dispatch(fetchUserStarted());
    const resp = await fetch(
      'https://lucyna-rest-api.onrender.com/admin/users',
      {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          authorization: 'Bearer ' + user.token,
        },
      },
    );
    const data = await resp.json();
    return dispatch(fetchUserSuccess(data));
  } catch (error) {
    return dispatch(fetchUserFailed(error.message));
  }
};

export const loginByToken = (user) => async (dispatch) => {
  try {
    const { payload } = await dispatch(fetchLoginByToken(user));
    if (payload.token !== undefined) await dispatch(fetchUserSuccess(payload));
    if (payload.token !== undefined) {
      localStorage.setItem('token', payload.token);
      localStorage.setItem('email', payload.user.email);
    }
  } catch (error) {
    console.log(error);
  }
};

export default slice.reducer;
