import React from 'react';
import Head from './Head';
import { useParams } from 'react-router-dom';
import useFetch from './customHooks/useFetch';
import { useNavigate } from 'react-router-dom';
//Redux
import { useSelector, useDispatch } from 'react-redux';
import { updateInformations } from '../reduxStore/login';

import cartSVG from '../img/cart.svg';
import marketplaceSVG from '../img/marketplace.svg';

const GamePage = () => {
  //Redux
  const dataUser = useSelector((state) => state.user);
  const dispatch = useDispatch();

  let library;
  let cart;
  let _id;

  if (dataUser.data !== null) {
    library = dataUser.data.user.library;
    cart = dataUser.data.user.cart;
    _id = dataUser.data.user._id;
  }

  const goToLogin = useNavigate();

  const buttonCart = React.useRef();
  const buttonBuyNow = React.useRef();

  const params = useParams();
  const [gameData, setGameData] = React.useState(null);
  const { request, data, loading, error } = useFetch();

  // Makes the Fetch
  React.useEffect(() => {
    async function fetchData() {
      const { json } = await request(
        `https://renanlucenadeveloper.github.io/Lucyna-API/${params.id}.json`,
      );
      setGameData(await json);
    }
    fetchData();
  }, [request, params]);

  // Disables the buttons if the game was already bought
  React.useEffect(() => {
    function alreadyHasGame() {
      if (gameData !== null) {
        if (
          gameData.price === 'soon' &&
          buttonCart.current &&
          buttonBuyNow.current
        ) {
          disableButtons.current('Wait For Release');
        }
      }

      if (cart !== undefined && library !== undefined) {
        if (cart[0][0] !== 'empty' || library[0][0] !== 'empty') {
          if (gameData !== null) {
            const alreadyHasLibrary = library.some((game) => {
              return game[0] === params.id;
            });

            const alreadyHasCart = cart.some((game) => {
              return game[0] === params.id;
            });

            if (buttonCart.current && buttonBuyNow.current) {
              if (alreadyHasLibrary) {
                disableButtons.current('Already in Library');
              } else if (alreadyHasCart) {
                disableButtons.current('Already in Cart');
              }
            }
          }
        }
      }
    }
    alreadyHasGame();
  }, [params.id, gameData, cart, library]);

  // Creates a ref to the function that will activate both inside the effect and outside of it
  const disableButtons = React.useRef();
  disableButtons.current = (text) => {
    buttonCart.current.remove();
    buttonBuyNow.current.setAttribute('disabled', '');
    buttonBuyNow.current.innerHTML = text;
  };

  function handleBuyNow() {
    if (library === undefined) {
      goToLogin('/login');
    }
    let informations;
    if (library[0][0] !== 'empty' && library !== undefined) {
      informations = {
        library: [...library, [params.id, gameData.title, gameData.price]],
      };
    } else {
      informations = {
        library: [[params.id, gameData.title, gameData.price]],
      };
    }

    dispatch(updateInformations(informations, _id));
  }

  function handleAddToCart() {
    if (cart === undefined) {
      goToLogin('/login');
      return;
    }
    let informations;
    if (cart[0][0] !== 'empty' && cart !== undefined) {
      informations = {
        cart: [...cart, [params.id, gameData.title, gameData.price]],
      };
    } else {
      informations = {
        cart: [[params.id, gameData.title, gameData.price]],
      };
    }

    dispatch(updateInformations(informations, _id));
  }

  if (loading) return <div className="loading"></div>;
  if (data)
    return (
      <>
        <Head
          title={`Lucyna | ${gameData.title}`}
          description="Buy the game to test the cart system"
        />
        <section id="game-page">
          <div>
            <h3 id="game-title">{gameData.title}</h3>
            <img
              src={gameData.thumbnail}
              alt={`${gameData.title} img`}
              id="game-image"
            />
          </div>
          <div>
            <p id="price">$ {gameData.price}</p>
            <p id="offer-data">Offer ends in 06/07/2023</p>
            <div id="buttons-container">
              <button
                ref={buttonCart}
                id="cart-button"
                onClick={() => {
                  handleAddToCart();
                }}
              >
                <img src={cartSVG} alt="Cart SVG" />
              </button>
              <button
                ref={buttonBuyNow}
                id="buy-now-button"
                onClick={() => {
                  handleBuyNow();
                }}
              >
                <img src={marketplaceSVG} alt="Buy Now SVG" />
                Buy Now
              </button>
            </div>

            <div id="genres">
              <h3>Genres:</h3>
              <p>{gameData.genres.join(', ')}</p>
            </div>

            <ul id="thecnical-container">
              <li>
                <p>Developer</p>
                <p className="response">{gameData.developer}</p>
              </li>
              <li>
                <p>Publisher</p>
                <p className="response">{gameData.publisher}</p>
              </li>
              <li>
                <p>Release Date</p>
                <p className="response">{gameData.release_date}</p>
              </li>
            </ul>

            <p id="short-description">{gameData.short_description}</p>
          </div>
        </section>
      </>
    );
  if (error) return <p>An error ocurred</p>;
};

export default GamePage;
