import React from 'react';
import marketplaceSVG from '../img/marketplace.svg';
import accountCircleSVG from '../img/account-circle.svg';
import rocketIconSVG from '../img/rocket-icon.svg';
import emptyLibraryIMG from '../img/empty-library-img.jpg';
import { useNavigate } from 'react-router-dom';
//Redux
import { useSelector, useDispatch } from 'react-redux';
import { updateInformations } from '../reduxStore/login';

import Head from './Head';

const Cart = () => {
  //Redux
  const dataUser = useSelector((state) => state.user);
  const dispatch = useDispatch();

  let library;
  let cart;
  let _id;

  if (dataUser.data !== null) {
    library = dataUser.data.user.library;
    cart = dataUser.data.user.cart;
    _id = dataUser.data.user._id;
  }

  const total = React.useRef(0);
  const backToStore = useNavigate();

  const buttonBuyNow = React.useRef();

  function handleBuyNow() {
    let informations;
    if (library[0][0] !== 'empty' && cart !== undefined) {
      informations = {
        cart: [['empty']],
        library: [...library, ...cart],
      };
    } else {
      informations = {
        cart: [['empty']],
        library: [...cart],
      };
    }

    dispatch(updateInformations(informations, _id));
    setTimeout(() => {
      backToStore('/store');
    }, 800);
  }

  return (
    <>
      <Head title="Cart" description="Test the Cart syste of my project" />

      <h2>Cart</h2>
      <section className="games-container-cart">
        {cart[0][0] !== 'empty' &&
          cart.map((game) => {
            total.current = total.current + Number(game[2]);
            return (
              <div key={game[0]} className="game-card">
                <h3 className="name">{game[1]}</h3>
                <p className="price-library">$ {game[2]}</p>
              </div>
            );
          })}

        {/* Empty Library container */}
        {cart[0][0] === 'empty' && (
          <div>
            <div className="empty-card">
              <p>It's kinda empty here</p>
            </div>
            <img
              src={emptyLibraryIMG}
              alt="Ilustration for empty Library"
              className="empty-img"
            />
          </div>
        )}
      </section>
      {cart[0][0] !== 'empty' && (
        <>
          <section>
            <div className="total">
              <h3 className="name">Total</h3>
              <p className="price-library">$ {total.current}</p>
            </div>
            <button
              ref={buttonBuyNow}
              onClick={() => {
                handleBuyNow();
              }}
              id="button-buy-now-cart"
            >
              <img src={marketplaceSVG} alt="Buy Now SVG" />
              Buy Now
            </button>
          </section>
          <section className="tankhs">
            <p>THANKS FOR YOUR ATTENTION!</p>
          </section>
        </>
      )}

      <section className="info">
        <div>
          <img src={accountCircleSVG} alt="account-circle img" />
          <p>This website uses localStorage for user informations</p>
        </div>
        <div>
          <img src={rocketIconSVG} alt="rocket-icon img" />
          <p>
            Feel free to test every feature and call me to making a website for
            you
          </p>
        </div>
      </section>
      <section className="tankhs">
        <p>THANKS FOR YOUR ATTENTION!</p>
      </section>
    </>
  );
};

export default Cart;
