import React from 'react';
import { NavLink } from 'react-router-dom';
import { UserContext } from './context/UserContext';
//Redux
import { useSelector } from 'react-redux';

import storeSVG from '../img/store.svg';
import librarySVG from '../img/library.svg';
import cartSVG from '../img/cart.svg';
import logoSVG from '../img/logo.svg';
import loggedPNG from '../img/logged.png';
import unloggedSVG from '../img/unlogged.svg';

const Header = () => {
  const userItemsJSON = React.useContext(UserContext);
  const isDesktop = JSON.parse(userItemsJSON.isDesktop);

  const dataUser = useSelector((state) => state.user);
  let cart;

  let libraryLink = '/login';
  let cartLink = '/login';

  if (dataUser.data !== null && dataUser.data.error !== true) {
    cart = dataUser.data.user.cart;

    libraryLink = '/library';
    cartLink = '/cart';
  }

  return (
    <header>
      <nav>
        <ul>
          <li className="container-li">
            {isDesktop === true && (
              <NavLink to="/store" className="logo-link">
                <img src={logoSVG} alt="Logo img" id="header-logo" />
              </NavLink>
            )}
            <NavLink to="/store" id="store-button">
              <img src={storeSVG} alt="store icon" />
              Store
            </NavLink>
            <NavLink to={libraryLink}>
              <img src={librarySVG} alt="library icon" />
              Library
            </NavLink>
          </li>
          <li className="cart-login-container">
            {/*  Unlogged button  */}
            {dataUser.data === null && (
              <NavLink to="/login" className="profile-button">
                <img src={unloggedSVG} alt="unlogged-button" />
              </NavLink>
            )}

            {/*  Logged button  */}
            {dataUser.data !== null && (
              <NavLink to="/library" className="profile-button">
                <img src={loggedPNG} alt="unlogged-button" />
              </NavLink>
            )}

            <NavLink to={cartLink} id="cart-icon">
              <img src={cartSVG} alt="Cart icon" />
              {cart && (
                <span id="cart-count">
                  {cart[0][0] === 'empty' ? '0' : cart.length}
                </span>
              )}
            </NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
