import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { loginFetch } from '../../reduxStore/login';
import accountCircleSVG from '../../img/account-circle.svg';
import rocketIconSVG from '../../img/rocket-icon.svg';

import { Link } from 'react-router-dom';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const dispatch = useDispatch();
  const dataUser = useSelector((state) => state.user);

  async function handleSubmit(e) {
    e.preventDefault();
    dispatch(loginFetch({ email, password }));
  }

  return (
    <>
      <h1 className="login-register-h1">Login</h1>

      <form onSubmit={handleSubmit}>
        <label htmlFor="email">Email</label>
        <input
          type="email"
          name="email"
          id="email"
          value={email}
          onChange={({ target }) => {
            setEmail(target.value);
          }}
          required
        />

        <label htmlFor="password">Password</label>
        <input
          type="password"
          name="password"
          id="password"
          value={password}
          onChange={({ target }) => {
            setPassword(target.value);
          }}
          required
        />

        <button className="login-register">Login</button>
      </form>
      <Link to="/register" className="toggle-login-register">
        Create a new account
      </Link>
      {dataUser.loading && <p className="message-form-loading">Loading</p>}
      {dataUser.error.error && (
        <p className="message-form">{dataUser.error.message}</p>
      )}

      <section className="tankhs">
        <p>THANKS FOR YOUR ATTENTION!</p>
      </section>
      <section className="info">
        <div>
          <img src={accountCircleSVG} alt="account-circle img" />
          <p>This website uses localStorage for user informations</p>
        </div>
        <div>
          <img src={rocketIconSVG} alt="rocket-icon img" />
          <p>
            Feel free to test every feature and call me to making a website for
            you
          </p>
        </div>
      </section>
      <section className="tankhs">
        <p>THANKS FOR YOUR ATTENTION!</p>
      </section>
    </>
  );
};

export default Login;
